:root,
:root body {
  background: #fff;
  border: 0;
  color: #333;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  font-weight: 300 !important;
  margin: 0;
  padding: 0;
  vertical-align: top;
}

:root,
:root body,
:root :global(#container) {
  min-height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
}

:root * {
  font-weight: 300 !important;
}

:root :global(#container) > div {
  -ms-flex: 1 1;
      flex: 1 1;
}



